import router from "../../router.js";

export default {
	state() {
		return {
			user: null,
			token: null,
		};
	},
	getters: {
		isAuthenticated(state) {
			const token = localStorage.getItem("token");
			if (token) {
				state.token = token;
				return token;
			}
			return false;
		},
		loggedInUser(state) {
			return state.user;
		},
	},
	mutations: {
		setUser(state, payload) {
			(state.user = payload.user), (state.token = payload.token);
		},
	},
	actions: {
		login(context, payload) {
			return context.dispatch("auth", {
				...payload,
				mode: "login",
			});
		},
		signup(context, payload) {
			// Registration
			return context.dispatch("auth", {
				...payload,
				mode: "signup",
			});
		},
		auth(context, payload) {
			const mode = payload.mode;
			if (mode === "login") {
				// Auth happens here...
				const authedUsername = "ronaron";
				const authedPassword = "asd";
				if (
					payload.username === authedUsername &&
					payload.password === authedPassword
				) {
					// Authed

					const authedUser = {
						username: payload.username,
						password: payload.password,
					};
					const token = 123456789;

					localStorage.setItem("user", JSON.stringify(authedUser));
					localStorage.setItem("token", token);

					context.commit("setUser", {
						user: authedUser,
						token: token,
					});

					router.push("/auth/dashboard");
				}
			}
		},
	},
};
