import { createRouter, createWebHistory } from "vue-router";
import store from "./store/index.js";

import Landing from "./pages/Landing.vue";
import Signin from "./pages/Signin.vue";
import Main from "./pages/Main.vue";
import Dashboard from "./pages/Dashboard.vue";
import Charts from "./pages/Charts.vue";
import New from "./pages/New.vue";
import LearningSettings from "./pages/LearningSettings.vue";
import Learning from "./pages/Learning.vue";

const router = createRouter({
	history: createWebHistory(),
	routes: [
		{
			path: "/",
			component: Landing,
		},
		{
			path: "/signin",
			component: Signin,
		},
		{
			path: "/auth",
			component: Main,
			children: [
				{
					path: "dashboard",
					component: Dashboard,
				},
				{
					path: "learning",
					component: Learning,
					meta: {
						authIsRequired: true,
					},
				},
				{
					path: "learning-settings",
					component: LearningSettings,
					meta: {
						authIsRequired: true,
					},
				},
				{
					path: "charts",
					component: Charts,
					meta: {
						authIsRequired: true,
					},
				},
				{
					path: "new",
					component: New,
					meta: {
						authIsRequired: false,
					},
				},
			],
			meta: {
				authIsRequired: true,
			},
		},
	],
});

router.beforeEach((to, from, next) => {
	const authIsRequired = to.matched.some(
		(record) => record.meta.authIsRequired
	);
	const isLoggedIn = store.getters.isAuthenticated;
	console.log(isLoggedIn);
	if (!isLoggedIn && authIsRequired) {
		return next("/signin");
		// next();
	}
	next();
});

export default router;
